.Logo{
    font-family: 'Raleway', sans-serif;
    text-align: center;
    color: #fff;
    font-weight: 400;
    text-transform: uppercase;
    -webkit-animation: blur 1.35s ease-out infinite;
            animation: blur 1.35s ease-out infinite;
    text-shadow: 0px 0px 5px #fff, 0px 0px 7px #fff;
  }
  
  @-webkit-keyframes blur {
    from {
      text-shadow:0px 0px 10px #fff,
        0px 0px 5px #fff, 
        0px 0px 12.5px #fff,
        0px 0px 12.5px #fff,
        0px 0px 12.5px #fff,
        0px 0px 12.5px #fff,
        0px 0px 12.5px #fff,
        0px 0px 12.5px #fff,
        0px 0px 25px #fff,
        0px 0px 25px #fff,
        0px 0px 25px #7B96B8,
        0px 0px 75px #7B96B8,
        0px 5px 50px #7B96B8,
        0px 5px 50px #7B96B8,
        0px 5px 50px #7B96B8,
        0px 5px 50px #7B96B8,
        0px -5px 50px #7B96B8,
        0px -5px 50px #7B96B8;
    }}
  
  @keyframes blur {
    from {
      text-shadow:0px 0px 10px #fff,
        0px 0px 5px #fff, 
        0px 0px 12.5px #fff,
        0px 0px 12.5px #fff,
        0px 0px 12.5px #fff,
        0px 0px 12.5px #fff,
        0px 0px 12.5px #fff,
        0px 0px 12.5px #fff,
        0px 0px 25px #fff,
        0px 0px 25px #fff,
        0px 0px 25px #7B96B8,
        0px 0px 75px #7B96B8,
        0px 5px 50px #7B96B8,
        0px 5px 50px #7B96B8,
        0px 5px 50px #7B96B8,
        0px 5px 50px #7B96B8,
        0px -5px 50px #7B96B8,
        0px -5px 50px #7B96B8;
    }}

