.imageFondo {
  position: absolute;
  max-height: 30%;
  -webkit-animation: animate 5s linear infinite;
          animation: animate 5s linear infinite;
    -webkit-transform-origin:  bottom left;
            transform-origin:  bottom left;
}
.imageFondo1 {
  position: absolute;
    max-height: 30%;
    -webkit-animation: animate1 5s linear infinite;
            animation: animate1 5s linear infinite;
    -webkit-transform-origin: bottom left;
            transform-origin: bottom left;
}
.imageFondo2 {
  position: absolute;
  max-height: 30%;
  -webkit-animation: animate2 5s linear infinite;
          animation: animate2 5s linear infinite;
    -webkit-transform-origin: bottom left;
            transform-origin: bottom left;
}
.imageFondo3 {
  position: absolute;
  max-height: 30%;
  -webkit-animation: animate3 5s linear infinite;
          animation: animate3 5s linear infinite;
    -webkit-transform-origin: bottom left;
            transform-origin: bottom left;
}
.imageFondo4 {
  position: absolute;
  max-height: 30%;
  -webkit-animation: animate4 5s linear infinite;
          animation: animate4 5s linear infinite;
    -webkit-transform-origin: bottom left;
            transform-origin: bottom left;
}

@-webkit-keyframes animate {
    0%  {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}

@keyframes animate {
    0%  {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}
@-webkit-keyframes animate1 {
  0%  {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  50% {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
  100%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}
@keyframes animate1 {
  0%  {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  50% {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
  100%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}
@-webkit-keyframes animate2 {
  0%  {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  50% {-webkit-transform: rotate(10deg);transform: rotate(10deg);}
  100%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}
@keyframes animate2 {
  0%  {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  50% {-webkit-transform: rotate(10deg);transform: rotate(10deg);}
  100%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}
@-webkit-keyframes animate3 {
  0%  {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  50% {-webkit-transform: rotate(15deg);transform: rotate(15deg);}
  100%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}
@keyframes animate3 {
  0%  {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  50% {-webkit-transform: rotate(15deg);transform: rotate(15deg);}
  100%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}
@-webkit-keyframes animate4 {
  0%  {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  50% {-webkit-transform: rotate(20deg);transform: rotate(20deg);}
  100%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}
@keyframes animate4 {
  0%  {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  50% {-webkit-transform: rotate(20deg);transform: rotate(20deg);}
  100%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}

